(function() {
  'use strict';

  angular
    .module('tillerHolding')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr, $scope, $window, $document) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1476876673290;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }

    angular.element(document).ready(function () {
        $('.slide1').css('min-height',$(window).height())
    });

    $document.on('scroll', function() {
      // do your things like logging the Y-axis
      console.log($window.scrollY);

      if ( $window.scrollY > 950 ) {
        $(".stay-btns").removeClass("hide")
      } else {
        $(".stay-btns").addClass("hide")
      }

      // or pass this to the scope
      $scope.$apply(function() {
          //$scope.pixelsScrolled = $window.scrollY;
      })
    });
  }
})();

